import { Avatar, AvatarFallback } from "../../components/ui/avatar";
import { Button } from "../../components/ui/button";
import { Card } from "../../components/ui/card";
import { Input } from "../../components/ui/input";
import { ScrollArea } from "../../components/ui/scroll-area";
import { cn } from "../../lib/utils";

import { Bot, MessageCircle, Send, User, X } from "lucide-react";
import React, { useEffect, useRef, useState } from 'react';
import type { Message, ChatWidgetProps } from '../../types/index';



export const ChatWidget = ({
  initialMessages = [],
  placeholder = "Digite sua mensagem...",
  onSendMessage,
  position = "left",
  primaryColor = "hsl(var(--primary))",
  title = "Assistente Virtual",
  welcomeMessage = "Ol! Como posso ajud-lo hoje?",
  onToggle,
  defaultOpen = false,
  height = "max",
  className,
}: ChatWidgetProps) => {
  const [isOpen, setIsOpen] = useState(defaultOpen)
  const [messages, setMessages] = useState<Message[]>(
    initialMessages.length > 0 
      ? initialMessages 
      : [
          {
            id: "1",
            role: 'bot',
            content: welcomeMessage,
            createdAt: new Date(),
          },
        ]
  )
  const [inputValue, setInputValue] = useState("")
  const [isTyping, setIsTyping] = useState(false)
  const messagesEndRef = useRef<HTMLDivElement>(null)
  const inputRef = useRef<HTMLInputElement>(null)

  const scrollToBottom = () => {
    if (messagesEndRef.current) {
      messagesEndRef.current.scrollIntoView({ 
        behavior: "smooth",
        block: "end",
        inline: "nearest"
      });
    }
  }

  useEffect(() => {
    if (isOpen) {
      setTimeout(scrollToBottom, 100);
    }
  }, [isOpen]);

  useEffect(() => {
    scrollToBottom();
  }, [messages])

  useEffect(() => {
    if (isOpen && inputRef.current) {
      inputRef.current.focus()
    }
  }, [isOpen])

  const handleToggle = () => {
    const newState = !isOpen
    setIsOpen(newState)
    onToggle?.(newState)
  }

  const handleSendMessage = async () => {
    if (!inputValue.trim()) return

    const userMessage: Message = {
      id: Date.now().toString(),
      role: 'user',
      content: inputValue,
      createdAt: new Date(),
    }

    setMessages((prev) => [...prev, userMessage])
    setInputValue("")
    onSendMessage?.(inputValue)

    setIsTyping(true)
    setTimeout(() => {
      const botMessage: Message = {
        id: (Date.now() + 1).toString(),
        role: 'bot',
        content: `Obrigado pela sua mensagem: "${inputValue}". Como posso ajud-lo mais?`,
        createdAt: new Date(),
      }
      setMessages((prev) => [...prev, botMessage])
      setIsTyping(false)
    }, 1500)
  }

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === "Enter" && !e.shiftKey) {
      e.preventDefault()
      handleSendMessage()
    }
  }

  const positionClasses = {
    left: "hello-cli-left-6 hello-cli-bottom-6",
    right: "hello-cli-right-6 hello-cli-bottom-6",
  }

  const chatPositionClasses = {
    left: "hello-cli-left-6 hello-cli-bottom-24",
    right: "hello-cli-right-6 hello-cli-bottom-24",
  }

  const chatHeightClasses = {
    min: "hello-cli-h-96",
    med: "hello-cli-h-[30rem]",
    max: "hello-cli-h-[36rem]",
  }

  return (
    <div className={cn("hello-cli-root", "hello-cli-chatwidget-wrapper", className)}>
      <div className={cn("hello-cli-fixed hello-cli-bottom-0 hello-cli-right-0 hello-cli-w-full hello-cli-max-w-[400px]", "hello-cli-chatwidget-container")}>
        <div className={cn("hello-cli-fixed hello-cli-z-50", "hello-cli-button-container", positionClasses[position])}>
          <Button
            onClick={handleToggle}
            size="lg"
            className={cn(
              "hello-cli-h-14 hello-cli-w-14 hello-cli-rounded-full hello-cli-shadow-lg hover:hello-cli-shadow-xl hello-cli-transition-all hello-cli-duration-300 hello-cli-ease-in-out hello-cli-flex hello-cli-items-center hello-cli-justify-center",
              "hello-cli-chat-button",
              isOpen && "hello-cli-rotate-180"
            )}
            style={{ backgroundColor: primaryColor }}
          >
            {isOpen ? (
              <X className="hello-cli-h-6 hello-cli-w-6 hello-cli-transition-transform hello-cli-text-white" />
            ) : (
              <MessageCircle className="hello-cli-h-6 hello-cli-w-6 hello-cli-transition-transform hello-cli-text-white" />
            )}
          </Button>
        </div>

        {isOpen && (
          <div 
            className={cn(
              "hello-cli-fixed hello-cli-z-50 hello-cli-transition-all hello-cli-duration-300 hello-cli-ease-in-out hello-cli-bg-background",
              chatPositionClasses[position],
              "hello-cli-animate-slide-in",
              "hello-cli-chat-container"
            )}
          >
            <Card className={cn( 
              "hello-cli-w-80 hello-cli-shadow-2xl hello-cli-rounded-lg hello-cli-overflow-hidden hello-cli-flex hello-cli-flex-col",
              chatHeightClasses[height],
              "hello-cli-card"
            )}
              style={{
                width: '20rem',
                borderRadius: '0.5rem',
                overflow: 'hidden',
                display: 'flex',
                flexDirection: 'column',
                boxShadow: '0 25px 50px -12px rgba(0, 0, 0, 0.25)'
              }}
            >
              {/* Header */}
              <div
                className={cn(
                  "hello-cli-flex hello-cli-items-center hello-cli-justify-between hello-cli-p-4 hello-cli-border-b hello-cli-rounded-t-lg hello-cli-text-white hello-cli-shrink-0",
                  "hello-cli-header"
                )}
                style={{ 
                  backgroundColor: primaryColor,
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'space-between',
                  padding: '1rem',
                  borderBottom: '1px solid rgba(0,0,0,0.1)',
                  borderTopLeftRadius: '0.5rem',
                  borderTopRightRadius: '0.5rem',
                  color: 'white',
                  flexShrink: 0
                }}
              >
                <div className="hello-cli-flex hello-cli-items-center hello-cli-gap-2">
                  <Avatar className="hello-cli-h-8 hello-cli-w-8">
                    <AvatarFallback className="hello-cli-bg-white/20">
                      <Bot className="hello-cli-h-4 hello-cli-w-4" />
                    </AvatarFallback>
                  </Avatar>
                  <h3 className="hello-cli-font-semibold hello-cli-text-sm">{title}</h3>
                </div>
                <Button
                  variant="ghost"
                  size="sm"
                  onClick={handleToggle}
                  className="hello-cli-text-white hover:hello-cli-bg-white/20 hello-cli-h-8 hello-cli-w-8 hello-cli-p-0 hello-cli-flex hello-cli-items-center hello-cli-justify-center"
                >
                  <X className="hello-cli-h-4 hello-cli-w-4 hello-cli-text-white" />
                </Button>
              </div>

              {/* Messages Area */}
              <ScrollArea className={cn("hello-cli-flex-1 hello-cli-p-4 hello-cli-overflow-y-auto hello-cli-scroll-smooth", "hello-cli-messages-area")}
                style={{
                  flex: '1',
                  padding: '1rem',
                  overflowY: 'auto',
                  scrollBehavior: 'smooth'
                }}
              >
                <div className={cn("hello-cli-space-y-4 hello-cli-pb-4", "hello-cli-message-list")}
                  style={{ 
                    display: 'flex',
                    flexDirection: 'column',
                    gap: '1rem',
                    paddingBottom: '1rem'
                  }}
                >
                  {messages.map((message) => (
                    <div
                      key={message.id}
                      className={cn(
                        "hello-cli-flex hello-cli-gap-2",
                        "hello-cli-message-row",
                        message.role === 'user' ? "hello-cli-justify-end hello-cli-user-row" : "hello-cli-justify-start hello-cli-bot-row"
                      )}
                      style={{
                        display: 'flex',
                        gap: '0.5rem',
                        justifyContent: message.role === 'user' ? 'flex-end' : 'flex-start'
                      }}
                    >
                      {message.role === 'bot' && (
                        <Avatar className="hello-cli-h-6 hello-cli-w-6 hello-cli-mt-1">
                          <AvatarFallback className="hello-cli-bg-muted">
                            <Bot className="hello-cli-h-3 hello-cli-w-3" />
                          </AvatarFallback>
                        </Avatar>
                      )}
                      <div
                        className={cn(
                          "hello-cli-max-w-[70%] hello-cli-rounded-lg hello-cli-px-3 hello-cli-py-2 hello-cli-text-sm hello-cli-message-bubble",
                          message.role === 'user'
                            ? "hello-cli-bg-primary hello-cli-text-primary-foreground hello-cli-user-message"
                            : "hello-cli-bg-muted hello-cli-text-muted-foreground hello-cli-bot-message"
                        )}
                        style={{
                          borderRadius: '0.5rem',
                          backgroundColor: message.role === 'user' 
                            ? 'hsl(222.2, 47.4%, 11.2%)' 
                            : 'hsl(210, 40%, 96.1%)',
                          color: message.role === 'user'
                            ? '#ffffff'
                            : 'hsl(215.4, 16.3%, 46.9%)',
                          padding: '0.5rem 0.75rem',
                          maxWidth: '70%',
                          overflow: 'hidden'
                        }}
                      >
                        {message.content}
                      </div>
                      {message.role === 'user' && (
                        <Avatar className="hello-cli-h-6 hello-cli-w-6 hello-cli-mt-1">
                          <AvatarFallback className="hello-cli-bg-primary">
                            <User className="hello-cli-h-3 hello-cli-w-3 hello-cli-text-primary-foreground" />
                          </AvatarFallback>
                        </Avatar>
                      )}
                    </div>
                  ))}

                  {isTyping && (
                    <div className="hello-cli-flex hello-cli-gap-2 hello-cli-justify-start">
                      <Avatar className="hello-cli-h-6 hello-cli-w-6 hello-cli-mt-1">
                        <AvatarFallback className="hello-cli-bg-muted">
                          <Bot className="hello-cli-h-3 hello-cli-w-3" />
                        </AvatarFallback>
                      </Avatar>
                      <div className="hello-cli-bg-muted hello-cli-rounded-lg hello-cli-px-3 hello-cli-py-2">
                        <div className="hello-cli-flex hello-cli-gap-1">
                          <div className="hello-cli-w-2 hello-cli-h-2 hello-cli-bg-muted-foreground hello-cli-rounded-full hello-cli-animate-bounce" />
                          <div
                            className="hello-cli-w-2 hello-cli-h-2 hello-cli-bg-muted-foreground hello-cli-rounded-full hello-cli-animate-bounce"
                            style={{ animationDelay: "0.1s" }}
                          />
                          <div
                            className="hello-cli-w-2 hello-cli-h-2 hello-cli-bg-muted-foreground hello-cli-rounded-full hello-cli-animate-bounce"
                            style={{ animationDelay: "0.2s" }}
                          />
                        </div>
                      </div>
                    </div>
                  )}
                  <div 
                    ref={messagesEndRef} 
                    className="hello-cli-h-px hello-cli-w-full" 
                  />
                </div>
              </ScrollArea>

              {/* Input Area - Update the send button */}
              <div className={cn("hello-cli-p-4 hello-cli-border-t hello-cli-mt-auto hello-cli-bg-background hello-cli-shrink-0", "hello-cli-input-area")}
                style={{
                  padding: '1rem',
                  borderTop: '1px solid rgba(0,0,0,0.1)',
                  marginTop: 'auto',
                  backgroundColor: 'white',
                  flexShrink: 0
                }}
              >
                <div className={cn("hello-cli-flex hello-cli-gap-2", "hello-cli-input-container")}
                  style={{
                    display: 'flex',
                    gap: '0.5rem'
                  }}
                >
                  <Input
                    ref={inputRef}
                    value={inputValue}
                    onChange={(e) => setInputValue(e.target.value)}
                    onKeyPress={handleKeyPress}
                    placeholder={placeholder}
                    className={cn("hello-cli-flex-1", "hello-cli-input")}
                    style={{
                      flex: '1',
                      padding: '0.5rem',
                      borderRadius: '0.375rem',
                      border: '1px solid #e2e8f0'
                    }}
                  />
                  <Button
                    onClick={handleSendMessage}
                    size="sm"
                    disabled={!inputValue.trim()}
                    style={{ backgroundColor: primaryColor }}
                    className={cn("hello-cli-px-4 hello-cli-py-2 hello-cli-rounded-full hello-cli-flex hello-cli-items-center hello-cli-justify-center hello-cli-transition-all hover:hello-cli-opacity-90", "hello-cli-send-button")}
                  >
                    <Send className="hello-cli-h-4 hello-cli-w-4 hello-cli-text-white" />
                  </Button>
                </div>
              </div>
            </Card>
          </div>
        )}
      </div>
    </div>
  );
};